<template>
	<div class="VideoHeader">
		<video autoplay muted loop id="myVidexo">
		<source :src="require('@/assets/videos/mainHeader2.mp4')" type="video/mp4">
		Your browser does not support HTML5 video.
		</video>
		<div class="shadow">
			<slide1 class="d-none d-sm-block" />
		</div>
	</div>
</template>
<script>
import slide1 from './headerSlider/slide1.vue';
	export default {
		components:{slide1}
	}
</script>
<style>
	.VideoHeader{
		position: relative;
	}
	#myvidexo{
		position:absolute;
		width:100%;
		height: 100%;
		top: 0px;
		left: 0px;
	}
	.shadow{position:absolute; width:100%; height:100%; background-color:rgba(0,0,0,0.3); top:0px; left:0px; padding-left:20%;}
	video{
		width: 100%;
		height: auto;
	}
</style>



