                 <p>ryde upholds a Code of Conduct for all driver-partners so that all drivers would have a clear understanding of the  legal compliance, safety, security and service standards expected of them. Because ryde maintains a zero-tolerance  policy, infringements can result in suspension or termination of user access to the ryde platform. Every ryde drivers  shall agree to comply fully with this Code of Conduct:</p>

<p>1. COMPLIANCE WITH ALL LAWS, REGULATIONS, POLICIES AND GUIDELINE (a). You shall comply with all traffic laws, regulations, rules, policies and guidelines as well as this Code and any  further guidelines that may be issued by the Company from time to time. Failure to comply with any part of the code of  conduct will lead to Suspension or immediate Termination.</p>

<p>(b). It is your sole responsibility to ensure that your PDVL or TDVL license is valid and current. You understand that  providing private hire car services without a PDVL/TDVL or valid approval letter from LTA is an offence under the Road  Traffic Act and liable to a fine not exceeding $10000 or imprisonment for a term not exceeding 3 months, or both. You  further understand that your PDVL license is linked to the demerit point system by the Land Transport Authority (LTA).  If you reach the 18-demerit-point threshold by ryde, your ryde account will be terminated. This 18-demerit-point is  based on the point system by Traffic Police. You shall forthwith inform us of any revocation or suspension of your  Vocational Licence by the LTA or Driving Licence by Traffic Police.</p>

<p>(c). You are to declare your criminal record.</p>

<p>(d). You are required to have your vocational licence with you AT ALL TIMES and shall produce it for inspection to the  Partner Aide Team upon request.</p>

<p>(e). You should note that where you choose to use personal in-vehicle cameras for safety and security purposes, they are  neither endorsed nor prohibited by the Company, and the Company does not collect, use and disclose any Personal Data of  third parties from such in-vehicle recordings. The collection, use and disclosure of any such Personal Data is not  conducted on behalf of the Company. As such, it is your sole responsibility to comply with local privacy laws and  regulations, and you should obtain the consent of your passenger prior to using the personal in-vehicle cameras in  such vehicles. Personal Data is defined in ryde&rsquo;s Privacy Policy which can be viewed here.</p>

<p>(f). As part of the Company&rsquo;s continuous effort to improve service delivery to the passengers and ensure full compliance  with all applicable laws, regulations and Company&rsquo;s guidelines including this Code, the Partner Aide Team will conduct  random checks on drivers. You are required to co-operate courteously and comply with reasonable requests from the Partner Aide Team.</p>

<p>(g). You are prohibited from taking part in any illegal demonstrations against the Company, being a member of an unregistered  association, incite other drivers not to use the ryde application, boycott or threaten to boycott the ryde application or  any other acts (&ldquo;eg: social media and electronic media&rdquo;) that may be construed to be against the interests of the Company.</p>

<p>(h). You agree not to be involved in any activity that may impair or degrade the reputation of the Company and/or causes disruption  to the Company&rsquo;s operation or conspire with other drivers or any third party with the intention to commit the aforesaid acts.</p>

