<p>(j). You are strictly prohibited against committing any acts with the intention to cheat or defraud the Company whether  via any &lsquo;Promo Code&rsquo;, Incentives or otherwise which may cause the Company to suffer losses. Incentives will be forfeited  if you cheat or defraud the Company.</p>

<p>(k). You are to conduct yourself with decency within ryde&rsquo;s premises.(l) Like you, ryde Staff are entitled to a safe  working environment. Any offence committed against ryde&rsquo;s Staff will not be tolerated and may be reported to the authorities.  Any acts that are against the law will result in disciplinary action or legal prosecution.</p>

<p>You shall not physically and verbally abuse ryde&rsquo;s Staff You shall not threaten any ryde&rsquo;s staff. You shall not harass ryde&rsquo;s Staff by photo taking, video-taking or stalking 2. PROFESSIONAL AND QUALITY SERVICE (a). You shall provide safe and clean transportation to all passengers. The safety and comfort of passengers and drivers on  the ryde platform is of utmost priority. You need to maintain a driver&rsquo;s average rating of 4.3 and above, to avoid suspension. (with effect from 1st July 2019, it will be increased to 4.5)</p>

<p>(b). If a passenger believes a driver may be under the influence of drugs or alcohol, he or she has the right to end the trip  immediately and alert ryde 24hrs Call Centre or email through our online Helpdesk Centre.</p>

<p>The Company does not tolerate any consumption of drugs or alcohol by drivers while using the ryde platform. This is against  the law and a serious violation of the Company&rsquo;s safety policy. Further, illegal substances and open containers of alcohol  are not permitted in drivers&rsquo; vehicles. You are prohibited from having controlled items or keeping weapons of any kind or description in your vehicle or on your person  for any reason whatsoever. (c). You shall not refuse to provide services based on a person&rsquo;s race, religion, nationality, disability, sexual orientation, sex, marital status, gender identity, age or any other characteristic protected under law. This type of behavior can result in  termination of user access to the ryde platform. It is disrespectful to make derogatory or discriminatory remarks about a person  or group whether or not the passenger belongs to the group. (d) The Company takes a serious view on reckless driving which endangers  passengers and/or other road users and this includes:</p>

<p>Driving against the direction of traffic Failure to comply with the speed limit Failure to conform with traffic light Using your mobile phone without any hands free kit while driving Failure to use the indicator signal prior to changing lanes or turning into a junction Breaches of any other traffic laws (e). You shall always be properly and decently attired (at a minimum, you shall wear a collared shirt, long trousers and covered shoes)  and exhibit good mannerism as well as communicate with your passengers politely.(f) You are prohibited from driving without any valid  license, insurance policy and/or business licenses or permits required to provide transportation for hire.</p>

<p>(g). You are to always ensure that your passengers wear a seatbelt while riding in your vehicle.</p>

