<p>(i). You are strictly forbidden from committing any form of sexual offence, including making lewd comments/ texts, sexual harassment,  unwanted physical contact, unlawful restraint, drugging, molestation and rape. These are criminal offences under the law and a serious  violation of the Company&rsquo;s safety policy. Offenders will be prosecuted against the law. For better understanding, training materials  regarding sexual harassment is available to better understand what is not permissible.</p>

<p>(j). Bear in mind that harassment differs according to societal and personal norms &mdash; commenting on appearance, asking overly personal  questions and infringing into personal space are generally deemed inappropriate. We urge you to be mindful of other users&rsquo; privacy and  personal space. Texting or calling your passengers outside of professional grounds is both a form of harassment and a breach to his or  her entitlement to personal security and data protection.</p>

<p>(k). As a responsible transport provider, you are encouraged to provide assistance to handicapped passengers especially when entering or  alighting your vehicle.</p>

<p>(l). You shall accede to reasonable requests from your passengers such as lowering the air-conditioning and radio volume. You may help  passengers with their luggage if your physical ability permits.</p>

<p>(m). With regards to visually impaired passengers who are accompanied by their guide dogs, you are encouraged to accept such bookings  if it doesn&rsquo;t offend the tenets of your religion.</p>

<p>(n). Knowledge of traffic conditions such as road constructions, major accidents or outdoor events which may delay a trip is deemed an  advantage in your work and you are encouraged to always be alert to such developments. Do inform your passengers if you are running late.</p>

