<p>(q). Do not transfer a job to other drivers or any other person. If you are unable to pick up a passenger due to an emergency, please inform the passenger  immediately before cancelling the booking.</p>

<p>(r). Wait for your passenger to arrive at the pick-up point. Driving off without waiting for them provides a very negative experience for your customers.</p>

<p>(s). Do not set your own fares as this may be considered as overcharging. Collect the correct fixed fare / meter fare accordingly plus other charges such as toll &amp;  surcharge.</p>

