<p>(u). Do not recommend other ride booking apps to your passengers. This practice is annoying and unprofessional to passengers.</p>

<p>(v). Passengers do appreciate honesty on the part of drivers. Hence, you shall provide your service that is based on this principle. Acts of cheating such as failing  to give correct change for cash payment or taking a longer route than necessary (unless there is a valid reason for it or specifically requested by the passenger)  is prohibited.</p>

<p>(w). You are strictly prohibited from contacting your passenger through any channel or platforms unless it is for purposes related to the job booking made by such  passenger.</p>

<p>(x). You must not have another person or a pet inside the car during the time operating as a ryde driver.</p>

<p>(y). Respect your passenger&rsquo;s privacy. You MUST keep confidential all your passenger&rsquo;s personal information (i.e. name, mobile number and address) that comes into  your possession. Do not disclose or share any of your passenger&rsquo;s personal information with anyone, whether your friends or family members, or other passengers,  drivers or members of the public through any channel or platform.</p>

<p>(z). You shall not ask passengers to change payment method after accepting a job (Eg: Asking passengers to pay by cash instead of credit card)</p>

<p>(aa). You must only use the vehicle that corresponds to the vehicle description which has been provided by you to the Company for provision of the transportation  services. Do not take bookings if your car plate number differs from the number registered on the app.</p>

<p>(bb). Minimum credit wallet top up amount via all channels (bank transfer, M1 billing, transfer from cash wallet, etc) is $10. Any actions of manipulating the  assignment system will be deemed an abuse of the system and may result in your access being terminated.</p>

<p>(cc). Drivers are expected to achieve minimum performance levels on Acceptance Rate, Cancellation Rate, and Driver Ratings in order to receive bookings on the  rydeCar platform. Acceptance Rate cannot fall below 30%. Cancellation Rate cannot exceed 15%. Driver Ratings cannot fall below 4.3 (with effect from 1st July  2019, it will be increased to 4.5)</p>

<p>(dd). You shall maintain a proper working condition, clean and smoke-free LTA-compliant transportation at all times. You are not allowed to install inward facing video  camera that invade into the privacy of passengers. Installing inward camera without LTA approval is an offence. There are requirements set by LTA should any  approval is given.</p>

<p>(ee). The Company maintains a zero-tolerance policy regarding all infringements and violations of this Code of Conduct and the Driver acknowledges that this may  result in suspension or termination of user access to the ryde platform.</p>

<p>(ff). No-refund policy shall apply at all times regardless of your decision to terminate your usage, our decision to terminate or suspend your usage, disruption caused  to the Service either planned, accidental or intentional, or any reason whatsoever.</p>

<p>3. EMERGENCY PROCEDURE (a). When you are faced with a situation that requires immediate emergency attention, please call 999/995. Once all parties are safe and the situation has been handled  by the authorities, please then notify the Company promptly so we may assist and take appropriate action as needed, including cooperating with law enforcement.</p>

<p>(b). You are required to refer to the Standard Operating Procedure on Emergency for further information.</p>

<p>4. CONTINUING TRAINING AND IMPROVEMENT (a). At the Company, we are committed to continual improvement to service standards. Drivers are to be open to feedback and further training.</p>

<p>(b). The Company implements a two-way passenger and driver feedback system, as well as a mystery passenger audit system. We also enforce continual evaluation  of a driver&rsquo;s suitability and performance, and provide feedback accordingly. In most cases, we aim to work together with the driver via feedback sessions, training  events, workshops and audits etc. Results from these efforts depend on receptivity of the driver.</p>

<p>(c). You are required to attend the training sessions that are arranged by the Company or its agents unless you are faced with an emergency or due to other  unavoidable reasons. In such situations, replacement sessions may be arranged at a later date.</p>

<p>All drivers are required to acknowledge and declare that having a full understanding of all clauses stated in the above Driver&rsquo;s Code of Conduct and solely  responsible for any and all liability, which results or is alleged as a result of the operation of motor vehicle/passenger vehicle and/or taxi/passenger delivery service,  including, but not limited to personal injuries, death and property damages. Driver also stand culpable for damages sustained by ryde if there are found to have  contravened any of the clauses in the Driver&rsquo;s Code of Conduct. Furthermore, Drivers shall understand and agree that ryde has the right to take such appropriate  actions against drivers, including suspending or terminating the driver access to the ryde application in the event breach of any clauses of this Driver&rsquo;s Code of  Conduct. *Supplement Terms of Use is also known as code of conduct. Read more about ryde&rsquo;s quality policy here. Suggestions or comments? Write to us at  info@ryde.app &ndash; we look forward to hearing from you. </p>
