<template>
	<div class="screenShot">
		<div class="headerTitle" data-aos="zoom-in">
			Rider Steps
		</div>
		<div class="afterHeader" data-aos="zoom-in">
		</div>
		<carousel-3d style="height: 600px; background-color: transparent; border-width:0px; ">
		    <slide v-for="i in rider" :index="i.index" :key="i.index" style="height: 600px;">
		        <template >
		            <img :data-index="i.index" :src="i.img" style="height: 600px; width:307px;" data-aos="zoom-in">
		            <div class="te" style="text-align: center;">{{i.element}}</div>
		        </template>
		    </slide>
		</carousel-3d>
	</div>
</template>
<script>
export default {
	data() {
		return {
			rider:[
				{img:require('@/assets/steps/rider/1.png'),index:0,element:'a'},
				{img:require('@/assets/steps/rider/2.png'),index:1,element:'b'},
				{img:require('@/assets/steps/rider/3.png'),index:2,element:'c'},
				{img:require('@/assets/steps/rider/4.png'),index:3,element:'d'},
				{img:require('@/assets/steps/rider/5.png'),index:4,element:'e'},
				{img:require('@/assets/steps/rider/6.png'),index:5,element:'f'},
				{img:require('@/assets/steps/rider/7.png'),index:6,element:'g'},
				{img:require('@/assets/steps/rider/8.png'),index:7,element:'h'},
			]
		}
	}
	}
</script>
<style>
	.screenShot{padding: 90px 0px;width:100%; text-align: center;}
	.screenShot .headerTitle { font-size: 32px; color:black; font-weight: 600; text-align: center; }
	.screenShot .afterHeader{font-size: 18px; color:black;  padding: 5px 5px;}
	.Images{margin-top: 40px;}
	#app > div > div.screenShot > div:nth-child(3) > div { height:600px!important; }
	.carousel-3d-slide{border-width: 0px; border-color: transparent; background-color:transparent!important;}
	.te{position: absolute; top:0px; left:0px;}
	#app > div > div.screenShot > div.carousel-3d-container > div > div.carousel-3d-slide.current{border-width:0px;}
</style>


