<template>
    <div class="city">
        <navComponent />
        <allCountrysHeaderVue />


        <div class="container-fluid p-90">
            <div class="row">
                <div class="col-md-2" v-for="(i,index) in citys" :key="index">
                    <ul class="list-group" >
                        <li class="list-group-item city-list-item"><a :href="'#/city/' + i.title" class="cityLink">{{i.title}}</a></li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="mt-3"></div>
        <footerContent/>
    </div>    
</template>
<script>
import navComponent from '@/Components/Global/navComponent.vue';
import allCountrysHeaderVue from './components/allCountrysHeaderVue.vue';
import footerContent from '@/Components/Global/footerContent.vue';
export default {
    components:{navComponent,allCountrysHeaderVue, footerContent},
    data() {
        return {
            citys:[]
        }
    },
    mounted(){
    const vm = this;
    switch(vm.$route.params.country) {
        case 'Bahrain':
            vm.citys = [
                {title:'Manama'}
            ];
            break;

        case 'Bangladesh':
            vm.citys = [
                {title:'Dhaka'}
            ];
            break;

        case 'India':
            vm.citys = [
                {title:'NewDelhi'}
            ];
            break;

        case 'Indonesia':
            vm.citys = [
                {title:'NewDelhi'}
            ];
            break;

        case 'Iran':
            vm.citys = [
                {title:'Tehran'}
            ];
            break;

        case 'Iraq':
            vm.citys = [
                {title:'baghdad'},
                {title:'Nineveh'},
                {title:'Basrah'},
                {title:'Sulaymaniyah'},
                {title:'DhiQar'},
                {title:'Babylon'},
                {title:'Erbil'},
                {title:'AlAnbar'},
                {title:'Diyala'},
                {title:'Saladin'},
                {title:'Kirkuk'},
                {title:'Najaf'},
                {title:'Wasit'},
                {title:'Al-Qādisiyyah'},
                {title:'duhok'},
                {title:'Karbala'},
                {title:'Maysan'},
                {title:'AlMuthanna'},
                {title:'Halabja'},
            ];
            break;

        case 'Jordan':
            vm.citys = [
                {title:'Amman'}
            ];
            break;


        case 'KingdomOfBhutan':
            vm.citys = [
                {title:'Thimphu'}
            ];
            break;


        case 'Kuwait':
            vm.citys = [
                {title:'Kuwait'}
            ];
            break;


        case 'Lebanon':
            vm.citys = [
                {title:'beirut'}
            ];
            break;


        case 'Malaysia':
            vm.citys = [
                {title:'KualaLumpur'}
            ];
            break;


        case 'Oman':
            vm.citys = [
                {title:'Muscat'}
            ];
            break;


        case 'Pakistan':
            vm.citys = [
                {title:'Islamabad'}
            ];
            break;


        case 'Qatar':
            vm.citys = [
                {title:'Doha'}
            ];
            break;


        case 'SaudiArabia':
            vm.citys = [
                {title:'riyadh'}
            ];
            break;


        case 'SriLanka':
            vm.citys = [
                {title:'Colombo'}
            ];
            break;


        case 'UnitedArabEmirates':
            vm.citys = [
                {title:'Dubai'},
                {title:'AbuDhabi'},

            ];
            break;


        case 'Yemen':
            vm.citys = [
                {title:"Sana'a"},
            ];
            break;




        case 'Algeria':
            vm.citys = [
                {title:"Algeria"},
            ];
            break;

        case 'Egypt':
            vm.citys = [
                {title:"Cairo"},
                {title:"Alexandria"},
            ];
            break;

        case 'Libya':
            vm.citys = [
                {title:"Tripoli"},
            ];
            break;

        case 'Morocco':
            vm.citys = [
                {title:"Rabat"},
            ];
            break;
        

        case 'Sudan':
            vm.citys = [
                {title:"Khartoum"},
            ];
            break;

        case 'Tunisia':
            vm.citys = [
                {title:"Tunisia"},
            ];
            break;


        

        case 'France':
            vm.citys = [
                {title:"Paris"},
            ];
            break;

        case 'Germany':
            vm.citys = [
                {title:"Berlin"},
                {title:"Frankfurt"},
            ];
            break;

        case 'Italy':
            vm.citys = [
                {title:"Rome"},
            ];
            break;

        case 'Turkey':
            vm.citys = [
                {title:"Anqara"},
                {title:"Istanbul"},
            ];
            break;

        case 'UnitedKingdom':
            vm.citys = [
                {title:"London"},
            ];
            break;


        case 'Canada':
            vm.citys = [
                {title:"Ottawa"},
            ];
            break;


        case 'UnitedStates':
            vm.citys = [
                {title:"Chicago"},
                {title:"LosAngeles"},
                {title:"newyork"},
                {title:"phoenix"},
                {title:"SanDiego"},
                {title:"SanFrancisco"},
                {title:"Washington"},
            ];
            break;

        case 'Mexico':
            vm.citys = [
                {title:"mexico"},
            ];
            break;



        case 'Chili':
            vm.citys = [
                {title:"SantiagoChili"},
            ];
            break;

        case 'Brasilia':
            vm.citys = [
                {title:"Brasilia"},
            ];
            break;

    }

    }
}
</script>
<style>
.sliderCar { padding:40px 0px;}
.cityContent{font-weight: 600; font-size: 18px; text-align: center; padding:60px 0px;}
.boxess{padding: 0px 0px;}
.safteyBox{padding: 30px; background-color: rgba(0,0,0,0.1); border-radius: 5px; border:1px solid black;}
.sbHeader{text-align: left; font-weight: 600; font-size: 20px;}
.sbContent{font-weight: 700; text-align: left; padding-top: 10%; font-size: 20px;}
.sbHeaderR{text-align: right; font-weight: 600; font-size: 20px; }
.sbContentR{font-weight: 700; text-align: right;padding-top: 10%;font-size: 20px;}
.halfContent{padding:2% 0px; margin-top: 50px; background-attachment: fixed;background-position: center;background-repeat: no-repeat;background-size: cover;} 
.hCon{padding:5px; border-radius: 10px; background-color: rgba(0, 0, 0, 0.4); color:white;}
.videoShowcase{background-attachment: fixed;background-position: center;background-repeat: no-repeat;background-size: cover;  height: 1000px; background-color: black;}
.rydeCity{text-align: center; font-size: 28px; padding: 3px; background-color: rgba(255, 255, 255, 0.4); color: white; font-weight: 400;}
.city-list-item{background-color: rgba(255, 255, 255, 0.4); color: white; font-weight: 400; text-align: center; font-size: 18px; border-radius: 10px; margin-top:5px; cursor: pointer; transition: all 0.3s;}
.city-list-item:hover{background-color: rgba(255, 255, 255, 0.6);}
.cityLink{ text-decoration: none; color:white;}
.cityLink:hover{ text-decoration: none; color:white;}
.videoBackGroundContent{position:absolute;width:100%;height:100%;padding-top:75px;}
#myVideo{position: absolute;width:100%;height:100%;}
.p-90{padding:90px 0px; background-color: black;}
</style>


