<template>
    <div class="safetyContent">
        <navComponent/>
       <div class="headerVue">
        <img :src="require('@/assets/seafty/s4.jpg')" class="img-fluid" style="height:800px; width:100%;" alt="">

        <div class="flex-content">
            <div class="flex-container">
                <div class="row">
                <div class="flex-item">
                    <center>
                        <div class="flex-header">
                           Anti-harassment
                        </div>
                    </center>
                </div>
                </div>
            </div>
        </div>
    </div>

        <div class="sConetentLayout">
            <div class="container">
                <div class="bigTitle" >
                    Ryde’s Anti-Harassment Policy
                </div>
                <div class="sclContent">
                   <p>Safety is very important to us at Ryde. We strive to be Southeast Asia&rsquo;s safest and most trusted platform for all our users.</p>
<p>Our passengers ride with us because they trust that we can provide them with safe and convenient transportation. Our drivers partner with us because they trust that we have their interests at heart. Trust is the foundation to this ongoing relationship that allows us to drive Southeast Asia forward.</p>
<p>As such, we expect&nbsp;<strong>all users</strong>&nbsp;on our platform to behave appropriately and respect each other&rsquo;s boundaries. Do note that any form of physical contact or even compliments on his/her looks can been seen as sexual harassment.</p>
<p>Inappropriate behaviour has no place on our platform and we have a ZERO TOLERANCE policy on sexual harassment. We must always treat each other with courtesy and avoid all forms of disrespect or harassment.</p>
<h3><strong>What constitutes sexual harassment?</strong></h3>
<p>Sexual harassment is a criminal offence. It may be in any form of verbal or physical act of a sexual nature, including but not limited to:</p>
<p>(i) Suggestive comments or jokes;</p>
<p>(ii) Continuous personal advances or invitations;</p>
<p>(iii) Requests for sexual favours;</p>
<p>(iv) Unnecessary physical contact;</p>
<p>(vi) Spreading or displaying images with apparent sexual content, etc.</p>
<h3>How does Ryde take action?</h3>
<p>Ryde will take action against users who do not abide by these rules and this includes suspension from our App. In addition, Ryde will work with the authorities to investigate and seek support for the victims.</p>
<h3>Five simple rules to be mindful of when using our platform.</h3>
<p>To prevent miscommunication, we encourage all users to be mindful of one another&rsquo;s privacy and personal space. When in doubt, we recommend that they do the following:</p>
<p>#1&nbsp;<strong>The Golden Rule</strong>: NO physical contact.</p>
<p>#2&nbsp;<strong>Be respectful.</strong>&nbsp;Respect each other. Never use vulgarities, abusive language or suggestive and lewd remarks in speech or text.</p>
<p>#3&nbsp;<strong>Respect privacy.</strong>&nbsp;Do not post or share any driver or passenger information on any platform.</p>
<p>#4&nbsp;<strong>All booking information are confidential</strong>. Do not abuse this information or contact each other after the ride has been completed.</p>
<p>#5&nbsp;<strong>Avoid causing unnecessary alarm</strong>. All drivers and passengers are advised to stick to the GPS recommended route, unless it is mutually agreed otherwise. This helps prevent any misunderstanding between drivers and passengers.</p>
<p>We believe that everyone has a part to play in preventing sexual harassment. If you do encounter such inappropriate behaviour, do report it to the authorities in a timely manner. We will do everything we can to support official investigations of any criminal intent or act, in accordance with the law, and prevent &nbsp;its reoccurrence.</p>
<p><strong>Last updated on July&nbsp;14, 2019. Posted by Ryde Safety Team</strong></p>
<p>&nbsp;</p>
                </div>
            </div>
        </div>
    </div>    
</template>
<script>
import navComponent from '@/components/Global/navComponent.vue';
import safetyContent from './safetyContent/headerVue.vue';
export default {
    components:{navComponent,safetyContent}
}
</script>
<style>
.bigTitle{font-size: 26px; font-weight: 600; padding-top: 50px; color: turquoise;}
.sclContent{font-size: 18px; padding-top: 50px;}
.headerVue{position: relative;}
.flex-content{position: absolute; left: 0px; top: 0px; width: 100%; margin-top: 10%;}
.flex-container {padding: 0;margin: 0;list-style: none;display: -webkit-box;display: -moz-box;display: -ms-flexbox;display: -webkit-flex;display: flex;align-items: center;justify-content: center;align-content: center;}
.flex-row{width: 100%;}
.flex-item {align-self: center;color: white;font-weight: bold;text-align: center;}
.flex-header{background-color: rgba(0, 0,2, 0.15);text-align: center;font-size:32px;font-weight: 500;padding:40px 60px;border:1px solid black;border-radius:6px;}
.flex-contentArea{margin-top: 25px;text-align: center; font-size: xx-large;}
</style>



