<template>
	<div class="mapShow">
		<div class="mapImageHalf" >
			
			<center>
				<img :src="require('../../assets/mapNew.png')" class="img-fluid" alt="">
			</center>
			 <transition name="fade">
			 	<div class="circleImage pin1" v-if="pin"></div>
			 </transition>
			 <transition name="fade">
			 	<div class="circleImage pin2" v-if="pin"></div>
			 </transition>

		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				pin:false
			}
		},
		mounted() {
			const vm = this;
			setInterval(function(){ 
				vm.pin = !vm.pin;
			 }, 800);
		}
	}
</script>
<style>
	.mapShow{ flex:1; justify-content: center; align-items: center; align-content: center; margin: auto; background-color: #43c6db;}
	.mapImageHalf{
		background-repeat: no-repeat;
    	background-size: 50% 100%;
    	background-position: center center;
    	height: 580px;
    	position: relative;
		padding-top: 20px;
	}
	.circleImage {
		background-color: #ff4c8b;
		height: 10px;
		width: 10px;
		border-radius: 50px;
	}
	.pin1{
		position: absolute;
		left: 40%;
		top:10%;
	}
	.pin2{
		position: absolute;
		left: 54.5%;
		bottom: 250px;
	}
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
	  opacity: 0;
	}
</style>


