import Vue from 'vue'
import Router from 'vue-router'
import mainComponent from '@/components/mainComponent'
import driverFuture from '@/components/driverFuture';
import riderFuture from '@/components/riderFuture';
import safety from '@/components/safety';
import aboutus from '@/components/aboutus';
import help from '@/components/help';
import mainHelp from '@/components/help/screens/mainHelp.vue';
import theHelp from '@/components/help/screens/theHelp.vue';
import ourservice from '@/components/ourservice.vue';
// safty Screens 
import riderSafty from '@/components/safety/screens/riderSafty.vue';
import driverSafty from '@/components/safety/screens/driverSafty.vue';
import safetyContent from '@/components/safety/screens/safetyContent.vue';
import s1 from '@/components/safety/screens/s1.vue';
import s2 from '@/components/safety/screens/s2.vue';
import s3 from '@/components/safety/screens/s3.vue';
import s4 from '@/components/safety/screens/s4.vue';
//terms
import riderTerms from '@/components/safety/screens/riderTerms.vue';
import driverTerms from '@/components/safety/screens/driverTerms.vue';


// Citys
import city from '@/components/citys/city.vue';

import allCountrys from '@/components/citys/allCountrys.vue';
import allCitys from '@/components/citys/allCitys.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mainComponent',
      component: mainComponent
    },
    {
      path:'/driverFuture',
      name:'driverFuture',
      component:driverFuture
    },
    {
      path:'/riderFuture',
      name:'riderFuture',
      component:riderFuture
    },
    {
      path:'/safety',
      name:'safety',
      component:safety
    },
    {
      path:'/safty/riderSafty',
      name:'riderSafety',
      component:riderSafty
    },
    {
      path:'/safty/driverSafty',
      name:'driverSafty',
      component:driverSafty
    },
    {
      path:'/safty/s1',
      name:'s1',
      component:s1
    },
    {
      path:'/safty/s2',
      name:'s2',
      component:s2
    },
    {
      path:'/safty/s3',
      name:'s3',
      component:s3
    },
    {
      path:'/safty/s4',
      name:'s4',
      component:s4
    },
    {
      path:'/safty/riderTerms',
      name:'riderTerms',
      component:riderTerms
    },
    {
      path:'/safty/driverTerms',
      name:'driverTerms',
      component:driverTerms
    },
    {
      path:'/aboutus',
      name:'aboutus',
      component:aboutus
    },
    {
      path:'/help',
      name:'help',
      component:help
    },
    {
      path:'/help/mainHelp',
      name:'mainHelp',
      component:mainHelp
    },
    {
      path:'/help/theHelp',
      name:'theHelp',
      component:theHelp
    },
    {
      path:'/ourservice',
      name:'ourservice',
      component:ourservice
    },
    {
      path:'/city/:city',
      name:'city',
      component:city
    },
    {
      path:'/allCountrys/:qara',
      name:'allCountrys',
      component:allCountrys
    },
    {
      path:'/allCitys/:country',
      name:'allCitys',
      component:allCitys
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
});


