<template>
    <div class="city">
        <navComponent />
        <header-vue :slidesimage="slidesimage" />
            <div class="container">
                <div class="row">
                    <div class="col-md-2"></div>
                        <div class="col-md-8">
                            <div class="cityContent">
                                {{title}}
                            </div>
                        </div>
                    <div class="col-md-2"></div>
                </div>
            </div>
        <div class="mapImage">
            <center>
             <img :src="mapImage" class="img-fluid" style="width:100%; " alt="">
            </center>
        </div>
        <div class="sliderCar">
            <carousel-3d style="background-color: transparent; border-width:0px; width:100%; ">
                <slide v-for="i in cars" :index="i.index" :key="i.index" >
                    <template>
                        <center>
                            <img :data-index="i.index" :src="i.img" style="height: 250px;" data-aos="zoom-in">
                            <div class="title" style="text-align:center; font-weight:600;">
                                {{i.title}}
                            </div>
                        </center>
                    </template>
                </slide>
            </carousel-3d>
        </div>

        <showcase/>
        <div class="mt-3"></div>
        <footerContent/>

    </div>    
</template>
<script>
import navComponent from '@/Components/Global/navComponent.vue';
import headerVue from './components/headerVue.vue';
import showcase from '@/Components/Main/showcase.vue'
import footerContent from '@/Components/Global/footerContent.vue';
export default {
    components:{navComponent,headerVue, showcase,footerContent},
    data() {
        return {
             cars:[
                {index:0,title:'R 4',more:false,img:require('@/assets/carSlider/r4.png'),content:'4 seater car'},
                    {index:1,title:'R 6',more:false,img:require('@/assets/carSlider/r6.png'),content:'6 seater car'},
                    {index:2,title:'R suv',more:false,img:require('@/assets/carSlider/rsuv.png'),content:'Suv car'},
                    {index:3,title:'R lux',more:false,img:require('@/assets/carSlider/rlux.png'),content:'High Class Car'},
                    {index:4,title:'R taxi',more:false,img:require('@/assets/carSlider/rtaxi.png'),content:'Taxi'},
            ],
            slidesimage:[
                {img:require('@/assets/slider/slideNew1.jpg')},
                {img:require('@/assets/slider/slideNew2.jpg')},
                {img:require('@/assets/slider/slideNew3.jpg')},
            ],
            title:'',
            mapImage:require('@/assets/citys/maps/baghdad.png')
        }
    },
    mounted(){
    const vm = this;

    console.log(vm.$route.params.city);
   
   switch (vm.$route.params.city) {
        case 'baghdad':
           vm.title = 'Baghdad is the capital of Iraq. The population of Baghdad, as of 2016, is approximately 8,765,000, making it the largest city in Iraq, the second largest city in the Arab world, and the second largest city in Western Asia';
           break;
        
        case 'istanbul':
            vm.title = "Istanbul is a major city in Turkey that straddles Europe and Asia across the Bosphorus Strait. Its Old City reflects cultural influences of the many empires that once ruled here. In the Sultanahmet district, the open-air, Roman-era Hippodrome was for centuries the site of chariot races, and Egyptian obelisks also remain. The iconic Byzantine Hagia Sophia features a soaring 6th-century dome and rare Christian mosaics. ";
            break;
        
        case 'anqara':
            vm.title = "Ankara, Turkey’s cosmopolitan capital, sits in the country’s central Anatolia region. It’s a center for the performing arts, home to the State Opera and Ballet, the Presidential Symphony Orchestra and several national theater companies. Overlooking the city is Anitkabir, the enormous hilltop mausoleum of Kemal Atatürk, modern Turkey’s first president, who declared Ankara the capital in 1923.";
            break;

        case 'alexandria':
            vm.title = 'Alexandria is a Mediterranean port city in Egypt. During the Hellenistic period, it was home to a lighthouse ranking among the Seven Wonders of the Ancient World as well as a storied library. Today the library is reincarnated in the disc-shaped, ultramodern Bibliotheca Alexandrina. The city also has Greco-Roman landmarks, old-world cafes and sandy beaches. Its 15th-century seafront Qaitbay Citadel is now a museum.';
            break;

        case 'cairo':
            vm.title = 'Cairo, Egypt’s sprawling capital, is set on the Nile River. At its heart is Tahrir Square and the vast Egyptian Museum, a trove of antiquities including royal mummies and gilded King Tutankhamun artifacts. Nearby, Giza is the site of the iconic pyramids and Great Sphinx, dating to the 26th century BC. In Gezira Island’s leafy Zamalek district, 187m Cairo Tower affords panoramic city views.';
            break;
        
        case 'amman':
            vm.title = 'Amman, the capital of Jordan, is a modern city with numerous ancient ruins. Atop Jabal al-Qala’a hill, the historic Citadel includes the pillars of the Roman Temple of Hercules and the 8th-century Umayyad Palace complex, known for its grand dome. Built into a different downtown hillside, the Roman Theater is a 6,000-capacity, 2nd-century stone amphitheater offering occasional events.'
            break;

        case 'riyadh':
            vm.title = 'Riyadh, Saudi Arabia’s capital and main financial hub, is on a desert plateau in the country’s center. Business district landmarks include the 302m-high Kingdom Centre, with a sky bridge connecting 2 towers, and 267m-high Al Faisaliah Centre, with a glass-globe summit. In the historical Deira district, Masmak Fort marks the site of the 1902 raid that gave the Al Sauds control of Riyadh.'
            break;

        case 'beirut':
            vm.title = 'Beirut is the capital and largest city of Lebanon. No recent population census has been conducted, but 2007 estimates ranged from slightly more than 1 million to 2.2 million as part of Greater Beirut.';
            break;

        case 'newyork':
            vm.title = 'New York City comprises 5 boroughs sitting where the Hudson River meets the Atlantic Ocean. At its core is Manhattan, a densely populated borough that’s among the world’s major commercial, financial and cultural centers. Its iconic sites include skyscrapers such as the Empire State Building and sprawling Central Park. Broadway theater is staged in neon-lit Times Square.';
            break;

        case 'chicago':
            vm.title = 'Chicago, on Lake Michigan in Illinois, is among the largest cities in the U.S. Famed for its bold architecture, it has a skyline punctuated by skyscrapers such as the iconic John Hancock Center, 1,451-ft. Willis Tower (formerly the Sears Tower) and the neo-Gothic Tribune Tower. The city is also renowned for its museums, including the Art Institute of Chicago with its noted Impressionist and Post-Impressionist works.'
            break;

        case 'SanFrancisco':
            vm.title = 'San Francisco, officially City and County of San Francisco and colloquially known by its initialism SF, is a city in, and the cultural, commercial, and financial center of, Northern California'
            break;

        case 'phoenix':
            vm.title = "Phoenix is the capital of the southwestern U.S. state of Arizona. Known for its year-round sun and warm temperatures, it anchors a sprawling, multicity metropolitan area known as the Valley of the Sun. It's known for high-end spa resorts, Jack Nicklaus–designed golf courses and vibrant nightclubs. Other highlights include the Desert Botanical Garden, displaying cacti and numerous native plants.";
            break;

        case 'LosAngeles':
            vm.title = "Los Angeles is a sprawling Southern California city and the center of the nation’s film and television industry. Near its iconic Hollywood sign, studios such as Paramount Pictures, Universal and Warner Brothers offer behind-the-scenes tours. On Hollywood Boulevard, TCL Chinese Theatre displays celebrities’ hand- and footprints, the Walk of Fame honors thousands of luminaries and vendors sell maps to stars’ homes.";
            break;

        case 'SanDiego':
            vm.title = 'San Diego is a city on the Pacific coast of California known for its beaches, parks and warm climate. Immense Balboa Park is the site of the renowned San Diego Zoo, as well as numerous art galleries, artist studios, museums and gardens. A deep harbor is home to a large active naval fleet, with the USS Midway, an aircraft-carrier-turned-museum, open to the public.'
            break;

        case 'London':
            vm.title = "London, the capital of England and the United Kingdom, is a 21st-century city with history stretching back to Roman times. At its centre stand the imposing Houses of Parliament, the iconic ‘Big Ben’ clock tower and Westminster Abbey, site of British monarch coronations. Across the Thames River, the London Eye observation wheel provides panoramic views of the South Bank cultural complex, and the entire city.";
            break;

        case 'Paris':
            vm.title = "Paris, France's capital, is a major European city and a global center for art, fashion, gastronomy and culture. Its 19th-century cityscape is crisscrossed by wide boulevards and the River Seine. Beyond such landmarks as the Eiffel Tower and the 12th-century, Gothic Notre-Dame cathedral, the city is known for its cafe culture and designer boutiques along the Rue du Faubourg Saint-Honoré";
            break;
        
        case 'Berlin':
            vm.title = "Berlin, Germany’s capital, dates to the 13th century. Reminders of the city's turbulent 20th-century history include its Holocaust memorial and the Berlin Wall's graffitied remains. Divided during the Cold War, its 18th-century Brandenburg Gate has become a symbol of reunification. The city's also known for its art scene and modern landmarks like the gold-colored, swoop-roofed Berliner Philharmonie, built in 1963."
            break;

        case 'Frankfurt':
            vm.title = "Frankfurt, a central German city on the river Main, is a major financial hub that's home to the European Central Bank. It's the birthplace of famed writer Johann Wolfgang von Goethe, whose former home is now the Goethe House Museum. Like much of the city, it was damaged during World War II and later rebuilt. The reconstructed Altstadt (Old Town) is the site of Römerberg, a square that hosts an annual Christmas market."
            break;

        case 'Erbil':
            vm.title = "Erbil, also spelled Arbil, locally called Hewlêr by the Kurds, is the capital city of Iraqi Kurdistan and the most populated city in the Kurdish inhabited areas of Iraq. It is located approximately in the center of Iraqi Kurdistan region and the north of Iraq.";
            break;

        case 'Sulaymaniyah':
            vm.title = "Sulaymaniyah, also called Slemani, is a city in Iraqi Kurdistan. It is surrounded by the Azmer Range, Goyija Range and the Qaiwan Range in the northeast, Baranan Mountain in the south and the Tasluja Hills in the west.The city has a semi-arid climate with very hot dry summers and cool wet winters"
            break;

        case 'Karbala':
            vm.title = '';
            break;

        case 'duhok':
            vm.title = '';
            break;

        case 'Diyala':
            vm.title = '';
            break;

        case 'Halabja':
            vm.title = '';
            break;

        case 'Nineveh':
            vm.title = '';
            break;

        case 'Maysan':
            vm.title = '';
            break;

        case 'AlMuthanna':
            vm.title = '';
            break;

        case 'DhiQar':
            vm.title = '';
            break;

        case 'Saladin':
            vm.title = '';
            break;

        case 'Babylon':
            vm.title = '';
            break;

        case 'Najaf':
            vm.title = '';
            break;

        case 'Babylon':
            vm.title = '';
            break;

        case 'Al-Qādisiyyah':
            vm.title = '';
            break;

        case 'Basrah':
            vm.title = '';
            break;

        case 'AlAnbar':
            vm.title = '';
            break;

        case 'Kirkuk':
            vm.title = '';
            break;

        case 'Wasit':
            vm.title = '';
            break;

        case 'beirut':
            vm.title = '';
            break;

        case 'Bamako':
            vm.title = '';
            break;

        case 'Colombo':
            vm.title = '';
            break;

        case 'Dhaka':
            vm.title = '';
            break;

        case 'Islamabad':
            vm.title = '';
            break;

        case 'Jakarta':
            vm.title = '';
            break;

        case 'KualaLumpur':
            vm.title = '';
            break;

        case 'mexico':
            vm.title = '';
            break;
            
        case 'NewDelhi':
            vm.title = '';
            break;

        case 'Rome':
            vm.title = '';
            break;

        case 'SantiagoChili':
            vm.title = '';
            break;

        case 'SouthAfrica':
            vm.title = '';
            break;

        case 'Tehran':
            vm.title = '';
            break;

        case 'Thimphu':
            vm.title = '';
            break;

        case 'AbuDhabi':
            vm.title = '';
            break;

        case 'Ottawa':
            vm.title = '';
            break;

        case 'Algeria':
            vm.title = '';
            break;

        case 'Khartoum':
            vm.title = '';
            break;

        case 'Doha':
            vm.title = '';
            break;

        case 'Rabat':
            vm.title = '';
            break;

        case 'Sweden':
            vm.title = '';
            break;

        case 'Kuwait':
            vm.title = '';
            break;

        case 'Manama':
            vm.title = '';
            break;

        case 'Brasilia':
            vm.title = '';
            break;

        case 'Tunisia':
            vm.title = '';
            break;

        case 'Dubai':
            vm.title = '';
            break;

        case "Sana'a":
            vm.title = '';
            break;

        case "Tripoli":
            vm.title = '';
            break;

        case "Muscat":
            vm.title = '';
            break;

        case "Mogadishu":
            vm.title = '';
            break;

        case "Nouakchott":
            vm.title = '';
            break;

        case "Washington":
            vm.title = '';
            break;
        }

        vm.slidesimage = [
            {img:require('@/assets/citys/redy/' + vm.$route.params.city + '/1.png')},
            {img:require('@/assets/citys/redy/' + vm.$route.params.city + '/2.png')},
            {img:require('@/assets/citys/redy/' + vm.$route.params.city + '/3.png')},
        ];

        vm.mapImage = require('@/assets/citys/redy/' + vm.$route.params.city + '/map.png');
    }
}
</script>
<style>
.sliderCar { padding:40px 0px;}
.carousel-3d-slide{border-width: 0px; border-color: transparent; background-color:transparent!important;}
#app > div > div.sliderCar > div > div {perspective:400!important;}
.cityContent{font-weight: 600; font-size: 18px; text-align: center; padding:60px 0px;}
    .boxess{padding: 0px 0px; }
    .safteyBox{padding: 30px; background-color: rgba(0,0,0,0.1); border-radius: 5px; border:1px solid black;}
    .sbHeader{text-align: left; font-weight: 600; font-size: 20px;}
    .sbContent{font-weight: 700; text-align: left; padding-top: 10%; font-size: 20px;}
    .sbHeaderR{text-align: right; font-weight: 600; font-size: 20px; }
    .sbContentR{font-weight: 700; text-align: right;padding-top: 10%;font-size: 20px;}
    .halfContent{padding:2% 0px; margin-top: 50px; background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;} 
  .hCon{padding:5px; border-radius: 10px; background-color: rgba(0, 0, 0, 0.4); color:white;}
</style>


