<template>
    <div class="safetyContent">
        <navComponent/>
       <div class="headerVue">
        <img :src="require('@/assets/seafty/s3.jpg')" class="img-fluid" style="height:800px; width:100%;" alt="">

        <div class="flex-content">
            <div class="flex-container">
                <div class="row">
                <div class="flex-item">
                    <center>
                        <div class="flex-header">
                           Data Security
                        </div>
                    </center>
                </div>
                </div>
            </div>
        </div>
    </div>

        <div class="sConetentLayout">
            <div class="container">
                <div class="bigTitle" >
                    Security
                </div>
                <div class="sclContent">
                    <p>Data security and user privacy matter to us. While we take every possible measures to ensure our systems are well protected to safeguard your privacy, we still need your collaboration to make the Internet a better place. If you believe you have discovered a security vulnerability in our system that could adversely impact Ryde&rsquo;s platform or its customers, we appreciate your help in disclosing it to us in a responsible and timely manner.</p>
<h3>Reporting an online security issue</h3>
<p>If you suspect any online security issues or malicious cyberattacks that could compromise our online systems, you can&nbsp;send an email&nbsp;to our security team at&nbsp;<strong>info@ryde.llc</strong>&nbsp;after&nbsp;which you will receive an automated email from <strong>info@ryde.llc</strong>&nbsp; with a link&nbsp;to submit your bug report. We will be using the our platform for&nbsp;reviewing your bug report&nbsp;and bounty will be awarded, if found eligible.</p>
<p><strong>Examples of security issues and malicious cyberattacks:</strong><br /> ● Any kind of denial of service (DoS) attack<br /> ● Automated scanning<br /> ● Unauthorised access to Ryde&rsquo;s platform using backdoors, trojans or malware<br /> ● Attempts to breach confidential data<br /> ● Attempts to attack Ryde&rsquo;s platform</p>
<p><strong>Note:</strong>&nbsp;Legal actions will be taken against all attempts to compromise Ryde&rsquo;s platform and database.</p>
<p>When you contact us, please provide as much information as possible to help us with the investigation:<br /> ● A detailed description of the issue<br /> ● Steps to reproduce the issue</p>
<h3>Our promise to you:</h3>
<ul>
<li>Prompt acknowledgement of the report (within 2 business days)<br /> ● Transparency throughout the investigation process<br /> ● Utmost efforts to revolve the issues</li>
</ul>
<p>As a token of appreciation, we will reward those who are the first ones to report an issue.</p>
<h3>Responsible Disclosure Policy</h3>
<p>At Ryde, the security of our users and our platform are of utmost priority. If you believe you have discovered a potential vulnerability on our platform, apps or services, we would appreciate your help in fixing it fast by revealing your findings in accordance with this policy.<br /> Going public with security vulnerabilities can elevate the level of risk, so we urge you to keep such matters private until they can be addressed.</p>
<p>&nbsp;</p>
                </div>
            </div>
        </div>
    </div>    
</template>
<script>
import navComponent from '@/components/Global/navComponent.vue';
import safetyContent from './safetyContent/headerVue.vue';
export default {
    components:{navComponent,safetyContent}
}
</script>
<style>
.bigTitle{font-size: 26px; font-weight: 600; padding-top: 50px; color: turquoise;}
.sclContent{font-size: 18px; padding-top: 50px;}
.headerVue{position: relative;}
.flex-content{position: absolute; left: 0px; top: 0px; width: 100%; margin-top: 10%;}
.flex-container {padding: 0;margin: 0;list-style: none;display: -webkit-box;display: -moz-box;display: -ms-flexbox;display: -webkit-flex;display: flex;align-items: center;justify-content: center;align-content: center;}
.flex-row{width: 100%;}
.flex-item {align-self: center;color: white;font-weight: bold;text-align: center;}
.flex-header{background-color: rgba(0, 0,2, 0.15);text-align: center;font-size:32px;font-weight: 500;padding:40px 60px;border:1px solid black;border-radius:6px;}
.flex-contentArea{margin-top: 25px;text-align: center; font-size: xx-large;}
</style>



