<template>
    <div class="sliderCar">
        <carousel-3d style="background-color: transparent; border-width:0px; width:100%; ">
		    <slide v-for="i in rider" :index="i.index" :key="i.index" >
		        <template>
                    <center>
		                <img :data-index="i.index" :src="i.img" style="height: 250px;" data-aos="zoom-in">
                        <div class="title" style="text-align:center; font-weight:600;">
                            {{i.title}}
                        </div>
                    </center>
		        </template>
		    </slide>
		</carousel-3d>
    </div>    
</template>
<script>
export default {
    data() {
        return {
            rider:[
                {index:0,title:'R 4',more:false,img:require('@/assets/carSlider/r4.png'),content:'4 seater car'},
                    {index:1,title:'R 6',more:false,img:require('@/assets/carSlider/r6.png'),content:'6 seater car'},
                    {index:2,title:'R suv',more:false,img:require('@/assets/carSlider/rsuv.png'),content:'Suv car'},
                    {index:3,title:'R lux',more:false,img:require('@/assets/carSlider/rlux.png'),content:'High Class Car'},
                    {index:4,title:'R taxi',more:false,img:require('@/assets/carSlider/rtaxi.png'),content:'Taxi'},
            ]
        }
    }
}
</script>
<style>
.sliderCar { padding:40px 0px;}
.carousel-3d-slide{border-width: 0px; border-color: transparent; background-color:transparent!important;}
#app > div > div.sliderCar > div > div {perspective:400!important;}
.carousel-3d-slide{border-width: 0px!important; border-color: transparent; background-color:transparent!important;}
</style>


