<template>
    <div class="slicerOne mt-5">
        <div class="container-fluid ">
            <div class="row ">
                <div class="col-md-6" style="padding:0px; margin:0px;">
                    <div class="soBoxOne">
                        <div class="font-weight-black text-center" style="font-size:32px;" data-aos="fade-up">
                             Contact us <i class="fa fa-angle-right" aria-hidden="true"></i>
                        </div>
                    </div>
                    <div class="soBoxTow">
                        <div class="font-weight-black text-center" style="font-size:32px;" data-aos="fade-up">
                          send Message <i class="fa fa-angle-down" aria-hidden="true"></i>  
                        </div>
                    </div>
                </div>
                <div class="col-md-6" style="padding:0px; margin:0px;">
                    <div class="soBoxOtherSide">
                        <div class="header " style="color:white; font-size:18px;" data-aos="fade-up">
                            Ryde LLC 
                           <br/>
                           info@ryde.llc
                        </div>
                        <div class="ssContentt pt-3" style="color:white; width:70%;">

                        </div>
                    </div>
                </div>
            </div> 
            <div class="realitve">
            <GmapMap
            :center="{lat:10, lng:10}"
            :zoom="3"
            map-type-id="terrain"
            style="width: 100%; height: 600px;"
            >
            </GmapMap>
            <div class="formOnMap">
                    <div class="container">
                        <div class="row wrap">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <input type="text" class="form-control" placeholder="Name">
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <input type="text" class="form-control" placeholder="Phone Number">
                                </div>
                            </div>
                            <div class="col-md-12">
                                <div class="form-group">
                                    <input type="text" class="form-control" placeholder="Email">
                                </div>
                            </div>
                            <div class="col-md-12">
                                <div class="form-group">
                                    <textarea class="form-control" id="content" placeholder="Message" rows="5"></textarea>
                                </div>
                            </div>
                            <div class="col-md-12">
                                <button class="btn btn-success btn-block">Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
             
        </div> 
    </div>    
</template>
<script>
export default {
    mounted() {
        const vm = this;
        
    }
}
</script>
<style>
.soBoxOne{padding: 30px; border: 1px solid black; background-color: #f3efe5; height: 50%; }
.soBoxTow{padding: 30px; border: 1px solid black; background-color: #4bb6f8; height: 50%;}
.soBoxOtherSide{padding:30px;  background-color: #3a3a3a; height: 100%;}
.paddingPoxt{width:200px; height: 100px; background-color: rgba(0,0,0,0.3); border:1px solid black; text-align: center; float: right;}
.blockPadding{padding:10px 0px; text-align: center; background-color: rgba(0,0,0,0.6); color: white; margin-top: 10px; border-radius: 5px;}
.formOnMap{position:absolute; left:0; top:0; z-index: 1000; padding:10% 30%; width:100%;}
.realitve{ position: relative; }
</style>



