<template>
    <div class="boxess">
        <div class="container pt-0 mt-0" v-for="(i,index) in items" :key="index">
            <div class="row mt-5" v-if="i.reverse">
                <div class="col-md-3">
                    <center>
                        <img :src="i.image" class="img-fluid" width="200" height="200" alt="">
                    </center>
                </div>
                <div class="col-md-6">
                    <div class="sbContent" v-html="i.title">
                        
                    </div>
                </div>
            </div>
            <div class="row mt-5" v-else>
                <div class="col-md-3"></div>
                <div class="col-md-6">
                    <div class="sbContentR" v-html="i.title">

                    </div>
                </div>
                <div class="col-md-3">
                    <center>
                        <img :src="i.image" class="img-fluid" width="200" height="200" alt="">
                    </center>
                </div>
            </div>
        </div>

        <div class="halfContent" :style="{'background-image':'url(' + require('../../assets/parlex/one.png') + ')'}">
            <div class="container">
                <div class="row">
                    <div class="col-md-2 col-sm-12"></div>
                    <div class="col-md-4 col-sm-12">
                        <div class="font-weight-black hCon" style="font-size:22px;">
                            Great safety performers actively
                            seek feedback on their safety
                            leadership behaviors.
                        </div>
                    </div>
                    <div class="col-md-4 col-sm-12">
                        <div class="font-weight-black hCon" style="font-size:22px;">
                            World-class safety performers
                            understand that aligning resources
                            with objectives is critical
                        </div>
                    </div>
                    <div class="col-md-2 col-sm-12"></div>
                </div>
                <div class="row mt-5">
                    <div class="col-md-3"></div>
                    <div class="col-md-6">
                        <img :src="require('@/assets/seafty/b.png')" class="img-fluid" alt="">
                    </div>
                    <div class="col-md-3"></div>
                </div>
            </div>
        </div>
    </div>    
</template>
<script>
export default {
    data() {
        return {
            items:[
                {title:'Find out where to get your car seat inspected by a certified technician',image:require('@/assets/seafty/a1.png'),reverse:true},
                {title:'Find the car seat that fits your child’s current size and age and get age <p>-based recommendations</p>',image:require('@/assets/seafty/a2.png'),reverse:false},
                {title:'Choose the right seat. Learn the differencebetween rear-facing seat, forward-facing, boosters and belts.',image:require('@/assets/seafty/a3.png'),reverse:true},
            ]
        }
    }
}
</script>
<style>
    .boxess{padding: 0px 0px; }
    .safteyBox{padding: 30px; background-color: rgba(0,0,0,0.1); border-radius: 5px; border:1px solid black;}
    .sbHeader{text-align: left; font-weight: 600; font-size: 20px;}
    .sbContent{font-weight: 700; text-align: left; padding-top: 10%; font-size: 20px;}
    .sbHeaderR{text-align: right; font-weight: 600; font-size: 20px; }
    .sbContentR{font-weight: 700; text-align: right;padding-top: 10%;font-size: 20px;}
    .halfContent{padding:2% 0px; margin-top: 50px; background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;} 
  .hCon{padding:5px; border-radius: 10px; background-color: rgba(0, 0, 0, 0.4); color:white;}
</style>



