<template>
    <div class="safetyContent">
        <navComponent/>
       <div class="headerVue">
        <img :src="require('@/assets/seafty/s1.jpg')" class="img-fluid" style="height:800px; width:100%;" alt="">

        <div class="flex-content">
            <div class="flex-container">
                <div class="row">
                <div class="flex-item">
                    <center>
                        <div class="flex-header">
                           Code of Conduct
                        </div>
                    </center>
                </div>
                </div>
