        <div class="sConetentLayout">
            <div class="container">
                <div class="bigTitle" >
                  Ryde Privacy Policy
                </div>
                <div class="sclContent">
                   <p>Last modified on:&nbsp;14 july 2019</p>
<p>This Privacy Policy (&ldquo;Policy&rdquo;) describes how Ryde technology llc, its respective subsidiaries, affiliates, associated companies and jointly controlled entities (collectively &ldquo;Ryde&rdquo;, &ldquo;we&rdquo;, &ldquo;us&rdquo; or &ldquo;our&rdquo;) collect, use, process and disclose your Personal Data through the use of Ryde&rsquo;s Apps and Websites (including all mobile applications and websites operated by Ryde (respectively &ldquo;Apps&rdquo; and &ldquo;Websites&rdquo;), products, features and other services globally (collectively, &ldquo;Services&rdquo;).</p>
<p>This Policy applies to our customers, passengers, agents, vendors, suppliers, partners (such as driver and merchant partners), contractors and service providers (collectively &ldquo;you&rdquo;, &ldquo;your&rdquo; or &ldquo;yours&rdquo;)</p>
<p>&ldquo;Personal Data&rdquo; is any information which can be used to identify you or from which you are identifiable. This includes but is not limited to your name, nationality, telephone number, bank and credit card details, personal interests, email address, your image, government-issued identification numbers, biometric data, race, date of birth, marital status, religion, health information, vehicle and insurance information.</p>
<h1><strong>Collection of Personal Data</strong></h1>
<p>We collect Personal Data about you in the ways listed below.&nbsp;We may also combine the collected Personal Data with other Personal Data in our possession.</p>
<h2>You provide your Personal Data to us</h2>
<p>We collect your Personal Data when you voluntarily provide it to us. For example, you may provide your Personal Data to us when you:</p>
<ul>
<li>fill up a user profile or registration forms;</li>
<li>provide information to assess your eligibility to provide services as a Ryde driver-partner;</li>
<li>interact with our social media pages;</li>
<li>participate in contests or events organised by us;</li>
<li>use biometric features to identity yourself; and</li>
<li>fill up demographic information in surveys.</li>
</ul>
<h2>When our services are used</h2>
<p>Personal Data may be&nbsp;collected&nbsp;through the normal operation of&nbsp;our Apps, Websites,&nbsp;Services. Some examples are:</p>
<ul>
<li>your location (to&nbsp;detectpick-up locations and abnormal route variations);</li>
<li>feedback, ratings and compliments;</li>
<li>transaction information (such as payment method and distance travelled);</li>
<li>information about how you interacted with our Apps,Website or services (such as features used&nbsp;and&nbsp;content viewed);</li>
<li>device information (such as hardware model and serial number, IP address, file names and versions and advertising identifiers); and</li>
<li>personal data you enter in messageswhen you use our in-app&nbsp;communication</li>
</ul>
<h2>From other sources</h2>
<p>When we collect Personal Data from other sources, we make sure that that data is transferred to us in accordance with applicable laws. Such sources include:</p>
<ul>
<li>referral programmes;</li>
<li>our business partners, such as fleet partners, payment providers, ride-hailing partners and transport partners;</li>
<li>insurance and financial providers;</li>
<li>publicly available data;</li>
<li>governmental sources of data;</li>
<li>when our users add you as an emergency contact; and</li>
<li>marketing services providers or partners.</li>
</ul>
<h2>Personal Data about driver-partners</h2>
<p>If you are a driver-partner, we may collect:</p>
<ul>
<li>telematics data (such as your speed, acceleration, and braking data);</li>
<li>device data (such as accelerometer data, GPS location, your IMEI number and the names of apps you have installed on your device); and</li>
<li>your vehicle registration data.</li>
</ul>
<h2>Sensitive Personal Data</h2>
<p>Some of the information that we collect is sensitive in nature. This includes information such as national ID numbers, race, marital status, and your health or religious beliefs. We only collect this information when this is necessary to comply with legal or regulatory requirements.</p>
<h2>Personal Data of minors</h2>
<p>As a parent or legal guardian, please do not allow minors under your care to submit Personal Data to Ryde. In the event that such Personal Data of a minor is&nbsp;disclosed&nbsp;to Ryde, you hereby consent to the processing of the minor&rsquo;s Personal Data and accept and agree to be bound by this Policy and take responsibility for his or her actions.</p>
<h2>When you provide personal data of other individuals to us</h2>
<p>In some&nbsp;situations, you may provide Personal Data of other individuals (such as your spouse, family members or friends) to us. For example, you may add them as your emergency contact. If you&nbsp;provide us with their Personal Data, you represent and warrant that you have and you have obtained their consent for their&nbsp;Personal&nbsp;Data to be&nbsp;collected,&nbsp;used and disclosed as set out in this Policy.</p>
<h1><strong>Use of Personal Data</strong></h1>
<p>Ryde may use, combine and process your Personal Data for the following purposes (&ldquo;Purposes&rdquo;).</p>
<h2>Providing services and features</h2>
<p>Your Personal Data will be used to provide, personalise, maintain and improve our products and services. This includes using your Personal Data to:</p>
<ul>
<li>provide you with Services across our various business verticals;</li>
<li>engage you to provide&nbsp;Services;</li>
<li>create, administer and update your account;</li>
<li>verify your identity;</li>
<li>validate your&nbsp;rideand process payments;</li>
<li>offer, obtain, provide or facilitate insurance or financing solutions;</li>
<li>track the progress of your trip and&nbsp;detect abnormal trip&nbsp;variations;</li>
<li>enable features that personalise your App, such as&nbsp;lists&nbsp;of your favourite places and previous destinations;</li>
<li>perform internal operations necessary to provide our&nbsp;Services, including troubleshooting software bugs and operational problems, conducting data analysis, testing and research, monitoring and analysing usage and activity trends;</li>
<li>protect the security or integrity of the Services and any facilities or equipment used to make the Services available;</li>
<li>process and manage your rewards;</li>
<li>enable communications between our users;</li>
<li>process, manage or verify your application for subscription with Ryde and to provide you with subscribers&rsquo; benefits; and</li>
<li>enable our partners to manage and allocate fleet resources.</li>
</ul>
<h2>Safety and security</h2>
<p>We use your data to ensure the safety and security of our services and all users. This includes:</p>
<ul>
<li>screening driver and delivery partners before enabling their use of our&nbsp;Services;</li>
<li>identifying unsafe driving behaviour such as speeding,&nbsp;harsh braking and acceleration, and providing personalised feedback to driverpartners;</li>
<li>verifying&nbsp;youridentity&nbsp;when&nbsp;you&nbsp;log in to Ryde;</li>
<li>using device, location, profile, usage and other Personal Data to prevent, detect and combat fraud or unsafe activities;</li>
<li>sharing drivers and passengers&rsquo; location and details when the emergency button or the &ldquo;share my ride&rdquo; feature is activated;</li>
<li>monitoring compliance with our terms and conditions, policies and Driver&rsquo;s Code of Conduct; and</li>
<li>detecting, preventing and prosecuting crime.</li>
</ul>
<h2>Customer support</h2>
<p>We use Personal Data to resolve customer support issues. For example, we may:</p>
<ul>
<li>investigate and address concerns;</li>
<li>monitor and improve our customer support responses;</li>
<li>respond to questions, comments and feedback; and</li>
<li>inform&nbsp;youabout steps taken to resolve customer support issues.</li>
</ul>
<h2>Research and development and security</h2>
<p>We may use the Personal Data we collect for testing, research, analysis and product development. This allows us to understand and analyse your needs and preferences,&nbsp;protect your personal data,&nbsp;improve and enhance the safety and security of our services, develop new features, products and services, and facilitate insurance and finance solutions.</p>
<h2>Legal purposes</h2>
<p>We may use the Personal Data we collect to investigate&nbsp;and&nbsp;resolve&nbsp;claims or disputes, or as allowed or required by applicable law</p>
<p>We may also use your Personal Data when we are required, advised, recommended, expected or requested to do so by our legal advisors or any local or foreign legal, regulatory, governmental or other authority.</p>
<p>For example, we may use your Personal Data to:</p>
<ul>
<li>comply with court orders or other legal, governmental or regulatory requirements;</li>
<li>enforce our Terms of Serviceor other agreements; and</li>
<li>protect our rights or propertyin the event of a claim or dispute.</li>
</ul>
<p>We may also&nbsp;use&nbsp;your Personal Data in connection with mergers,&nbsp;acquisitions,&nbsp;joint ventures, sale of company assets, consolidation,&nbsp;restructuring, financing, business asset transactions, or acquisition of all or part of our business by another company.</p>
<h2>Marketing and promotions</h2>
<p>We may use your Personal Data to market Ryde and Ryde&rsquo;s partners&rsquo; sponsors&rsquo; and advertisers&rsquo; products, services, events or promotions. For example, we may:</p>
<ul>
<li>send you alerts, newsletters, updates, mailers, promotional materials, special privileges, festive greetings; and</li>
<li>notify, invite and manage your participation in our events or activities;</li>
</ul>
<p>We may communicate such marketing to you by post, telephone call, short message service, online messaging service, push notification by hand and by email.</p>
<p>If you wish to unsubscribe to the processing of your Personal Data for marketing and promotions, please click on the unsubscribe link in the relevant email or message. Alternatively, you may also update your preferences in our App settings.</p>
<h1><strong>Disclosure of Personal Data</strong></h1>
<p>We need to share Personal Data with various parties&nbsp;for the Purposes.&nbsp;These parties&nbsp;include:</p>
<h2>Other users</h2>
<p>For example, if you are a passenger, we may share your pick-up and drop-off locations with drivers.</p>
<p>If you are a driver, we may share your Personal Data with your passenger including your name and photo; your vehicle make, model, number plate, location and average rating.</p>
<h2>With third-parties</h2>
<p>For example, we may share a drivers&rsquo; location and name with third parties when a passenger uses the Share My Ride feature or activates the Emergency Button.</p>
<h2>With Ryde partners at your request</h2>
<p>For example, if you requested a service through a Ryde partner or used a promotion provided by a Ryde partner, Ryde may share your Personal Data with&nbsp;that&nbsp;Ryde partners. Our partners include partners that integrate with our&nbsp;App or our&nbsp;App integrates with,&nbsp;vehicle services&nbsp;partners, or business partners which Ryde collaborate with to deliver a promotion, competition or other specialised service.</p>
<h2>With the owner of Ryde accounts that you may use</h2>
<p>For example, your employer may receive trip data when you may&nbsp;use your employer&rsquo;s Ryde for Business&nbsp;account.</p>
<h2>With subsidiaries and affiliates</h2>
<p>We share Personal Data with our subsidiaries, associated companies, jointly controlled entities and affiliates.</p>
<h2>With Ryde&rsquo;s service providers and business partners</h2>
<p>We&nbsp;may provide Personal Data to&nbsp;our&nbsp;vendors, consultants, marketing partners, research firms, and other service providers or business partners. This includes:</p>
<ul>
<li>payment processors and facilitators;</li>
<li>background check and anti-money laundering service providers;</li>
<li>cloud storage providers;</li>
<li>marketing partners and marketing platform providers;</li>
<li>data analytics providers;</li>
<li>research partners, including those performing surveys or research projects in partnership with Ryde or on Ryde&rsquo;s behalf;</li>
<li>fleet and merchant partners;</li>
<li>insurance and financing partners; and</li>
<li>vehicle solutions partners, vendors or third-party vehicle suppliers.</li>
</ul>
<h2>With our legal advisors and governmental authorities</h2>
<p>We&nbsp;may share your Personal Data with our legal advisors, law enforcement officials, government authorities and other third parties.</p>
<h1><strong>Retention of Personal Data</strong></h1>
<p>We&nbsp;retains your Personal Data for as long you maintain your Ryde account.&nbsp;Once&nbsp;your&nbsp;Personal Data is no longer necessary for the Services or Purposes, or we no longer have a legal or business purpose for retaining your Personal Data,&nbsp;we&nbsp;take steps to prevent access or use of such Personal Data for any purpose other than compliance with this privacy policy, or for purposes of safety, security, fraud prevention and detection.</p>
<h1><strong>International transfers of Personal Data</strong></h1>
<p>Your Personal Data may be transferred from country, state and city (&ldquo;Home Country&rdquo;) in which you are present while using our services&nbsp;to another country, state and city (&ldquo;Alternate Country&rdquo;). You understand and consent to the transfer of your Personal Data from your Home Country to the Alternate Country.</p>
<h1><strong>Cookies</strong></h1>
<p>Ryde, and third parties with whom we partner, may use cookies, web beacons, tags, scripts, local shared objects such as HTML5 and Flash (sometimes called &ldquo;flash cookies&rdquo;), advertising identifiers (including mobile identifiers such as Apple&rsquo;s IDFA or Google&rsquo;s Advertising ID) and similar technology (&ldquo;Cookies&rdquo;) in connection with your use of the Websites and Apps. Cookies may have unique identifiers, and reside, among other places, on your computer or mobile device, in emails we send to you, and on our web pages. Cookies may transmit Personal Data about you and your use of the Service, such as your browser type, search preferences, IP address, data relating to advertisements that have been displayed to you or that you have clicked on, and the date and time of your use. Cookies may be persistent or stored only during an individual session.</p>
<p>Ryde may allow third parties to use Cookies on the Websites and Apps to collect the same type of Personal Data for the same purposes Ryde does for itself. Third parties may be able to associate the Personal Data they collect with other Personal Data they have about you from other sources. We do not necessarily have access to or control over the Cookies they use.</p>
<p>Additionally, we may share non-personally identifiable Personal Data with third parties, such as location data, advertising identifiers, or a cryptographic hash of a common account identifier (such as an email address), to facilitate the display of targeted advertising.</p>
<p>If you do not wish for your Personal Data to be collected via Cookies on the Websites, you may deactivate cookies by adjusting your internet browser settings to disable, block or deactivate cookies, by deleting your browsing history and clearing the cache from your internet browser. You may also be able to limit our sharing of some of this Personal Data through your mobile device settings, or by submitting your details&nbsp;<a href="https://www.google.com/url?q=http://bit.ly/2HX2vgZ&amp;sa=D&amp;ust=1552460169372000">here</a>.</p>
<h1><strong>Amendments and updates</strong></h1>
<p>Ryde shall have the right to modify, update or amend the terms of this Policy at any time by placing the updated Policy on the Websites. By continuing to use the Apps, Websites&nbsp;or&nbsp;Services, purchase products from Ryde or continuing to communicate or engage with&nbsp;Ryde following the modifications, updates or amendments to this Policy, you signify your acceptance of such modifications, updates or amendments.</p>
<p>If you have any queries about your personal data, please contact our Data Privacy Officer at:</p>
<p>Email: info@ryde.llc</p>
<p>The original of this Privacy Statement is written in the English language. In the event of any conflict between the English and other language versions, the English version shall prevail.</p>
<p>&nbsp;</p>
