<template>
	<div class="navComponent">
		<nav class="navbar navbar-expand-lg navbar-light bg-light static-top">
		  <div class="container">
		    <!-- <a class="navbar-brand" href="#/" style="font-family: 'Nonchalance'; font-weight:900; color:#43c6db; font-size:48px;" >Ryde</a> -->
			<a href="#/"><img :src="require('@/assets/logo.svg')" width="125" height="60" alt=""></a>
		    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
		          <span class="navbar-toggler-icon"></span>
		        </button>
		    <div class="collapse navbar-collapse" id="navbarResponsive">
		      <ul class="navbar-nav ml-auto">
		        <li class="nav-item ">
		          <a class="nav-link customNavLink" href="#/riderFuture" style="font-family: 'Cairo', sans-serif; font-size:18px;" >Ride</a>
		        </li>
		        <li class="nav-item">
		          <a class="nav-link customNavLink" href="#/driverFuture" style="font-family: 'Cairo', sans-serif; font-size:18px;" >Drive</a>
		        </li>
				<li class="nav-item">
		          <a class="nav-link customNavLink" href="#/" style="font-family: 'Cairo', sans-serif; font-size:18px;" >Business</a>
		        </li>
				<li class="nav-item">
		          <a class="nav-link customNavLink" href="#/" style="font-family: 'Cairo', sans-serif; font-size:18px;" >Delivery</a>
		        </li>
		        <li class="nav-item">
		          <a class="nav-link customNavLink" href="#/safety" style="font-family: 'Cairo', sans-serif; font-size:18px;">Safety</a>
		        </li>
		        <li class="nav-item">
		          <a class="nav-link customNavLink" href="#/ourservice" style="font-family: 'Cairo', sans-serif; font-size:18px;">Our Service</a>
		        </li>
		        <li class="nav-item">
		          <a class="nav-link customNavLink" href="#/aboutus" style="font-family: 'Cairo', sans-serif; font-size:18px;">About ryde</a>
		        </li>
		        <li class="nav-item">
		          <a class="nav-link customNavLink" href="#/help" style="font-family: 'Cairo', sans-serif; font-size:18px;">Help</a>
		        </li>
		      </ul>
		    </div>
		  </div>
		</nav>
		<div class="moreNavbar d-none d-sm-block" >
				<div class="container">
					<div class="row wrap">
						<div class="col-xl-1 col-lg-1 col-md-2 col-sm-3 col-4" v-for="(i,index) in items" :key="index" style="padding:0px;">
							<div class="moreNavbarBox">
								<div class="bottomTitle" @mouseover="showMore(index,'in')" @mouseleave="showMore(index,'out')">
									{{i.title}}
								</div>
								<transition name="fade">
									<div class="moreInfo" v-if="i.more">
										<img v-if="i.img" :src="i.img"  style="width:125px; height:75px;" alt="">
										<div class="mt-2">
											{{i.content}}
										</div>
									</div>
								</transition>
							</div>
						</div>
					</div>
				</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				items:[
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R 4',more:false,img:require('@/assets/carSlider/r4.png'),content:'4 seater car'},
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R 6',more:false,img:require('@/assets/carSlider/r6.png'),content:'6 seater car'},
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R suv',more:false,img:require('@/assets/carSlider/rsuv.png'),content:'Suv car'},
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R lux',more:false,img:require('@/assets/carSlider/rlux.png'),content:'High Class Car'},
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R taxi',more:false,img:require('@/assets/carSlider/rtaxi.png'),content:'Taxi'},
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R Bus',more:false,img:require('@/assets/carSlider/rbus.png'),content:'Bus'},
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R Share',more:false,content:'Shareing Car'},
					{icon:'<i class="fa fa-car" aria-hidden="true"></i>',title:'R Aid',more:false,content:'Aid Car '},
				]
			}
		},
		methods:{
			showMore(index,type) {
				const vm = this;
				if(type == 'in') {
					vm.items[index].more = true;
				}else {
					vm.items[index].more = false;
 				}
			}
		}
	}
</script>
<style>
.customNavLink{margin-left: 10px;margin-right: 20px; border-radius: 5px; color:#43c6db!important; font-weight: 900;}
.moreNavbar { background-color: #43c6db; color: white; }
.moreNavbarBox{padding: 10px 0px;text-align: center; cursor: pointer; transition: all 0.3s; position: relative; display: inline-block;}
.moreNavbarBox:hover {background-color: #43c6db; }
.headIcon {font-size: 20px; font-weight: 600; }
.bottomTitle{font-size: 18px; }
.moreInfo{position:absolute; z-index:1000; padding:5px 20px; background-color:white; border-radius:3px; border:1px solid black; color:black; font-weight: 700; font-size:16px;}
fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>


