<template>
    <div class="riderdriverBox">
        <div class="container">
            <div class="row">
                <div class="col-md-6 mt-5" v-for="(i,index) in items" :key="index">
                    <center>
                        <img :src="i.image" class="img-fluid" width="125" height="125" alt="">
                    </center>
                    <div class="ContentC">
                        <div class="ccTitle pt-1">
                            {{i.title}}
                        </div>
                        <div class="ccContent pt-1">
                            {{i.content}}
                        </div>
                        <div class="ccmore">
                            <a :href="'#/safty/' + i.link">More ></a>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 mt-5" >
                    <center>
                        <img :src="require('@/assets/seafty/terms.png')" class="img-fluid" width="125" height="125" alt="">
                    </center>
                    <div class="ContentC">
                        <div class="ccTitle pt-1">
                            Terms of Service
                        </div>
                        <div class="ccContent pt-1">
                            Conditions that apply to any usage of Rydes services
                        </div>
                        <div class="ccmore">
                            <a :href="'#/safty/driverTerms'">Driver ></a>
                            <a :href="'#/safty/riderTerms'">Rider ></a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            items:[
                {image:require('@/assets/seafty/c1.png'),title:'Code of Conduct',content:'Legal compliance, safety, security & service standards we expect of our partners.',link:'s1'},
                {image:require('@/assets/seafty/c2.png'),title:'Data Privacy',content:'Our promise to keep your personal data and information safe and secure.',link:'s2'},
                {image:require('@/assets/seafty/c3.png'),title:'Data Security',content:'Our commitment to ensuring our systems are well-protected to safeguard your privacy.',link:'s3'},
                {image:require('@/assets/seafty/c4.png'),title:'Anti-harassment',content:'Our zero-tolerance policy on sexual harassment to preserve a healthy ecosystem.',link:'s4'},
            ]
        }
    }
}
</script>
<style>
    .riderdriverBox{
        padding: 20px 0px;
        margin-top:20px;
    }
    .learnMore{text-align: right; color:royalblue; font-weight: 600; cursor: pointer; transition: all 0.3s;}
    .learnMore:hover {color:blue;}
    .ContentC {text-align: center; padding-top:10px;}
    .ccTitle{font-size: 20px; font-weight: 600;}
    .ccContent{font-weight: 500;}
</style>



