
<template>
    <div class="help">
        <navComponent/>
        <headerSection/>
        <div class="container mt-2 pa-90">
            <div class="row">
                <div class="col-md-3">
                    <ul class="list-group">
                        <li class="list-group-item helpListMain active">Main Help</li>
                        <li class="list-group-item helpListMain">Main Help 2</li>
                        <li class="list-group-item helpListMain">Main Help 3</li>
                        <li class="list-group-item helpListMain">Main Help 4</li>
                    </ul>
                </div>
                <div class="col-md-9">
                    <div class="container">
                        <div class="row">
                                <div class="mainHelp">
                                    Sub Help
                                </div>
                                <div class="subMainList pt-2">
                                    help Content help Content help Content help Content help Content help Content help Content help Content help Content help Content help Content 
                                </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footerContent/>
    </div>    
</template>
<script>
import navComponent from '@/components/Global/navComponent.vue';
import headerSection from '../headerSection.vue';
import footerContent from '@/components/Global/footerContent.vue';

export default {
    components:{navComponent,headerSection,footerContent}
}
</script>
<style>
.helpListMain{cursor: pointer; transition: all 0.3s; margin-top: 1px;}
.helpListMain:hover{background-color: rgba(0,0,0,0,0.3)!important; border: 1px solid black;}
.mainHelp{font-size: 24px; cursor: pointer;}
.subMainListUl{list-style-type: none; padding: 0px; margin:0px; padding-left:10px;}
.subMainListUl > li {cursor: pointer;}
.subMainListUl > li:hover {color:cornflowerblue;}
.learnMoreHelp{color:blue;}
.pa-90{padding:90px 0px; }
</style>



