<template>
	<div class="main">
		<navComponent></navComponent>
		<headerSlider></headerSlider>
		<aboutus></aboutus>
		<future></future>
		<showcase></showcase>
		<videoShowcase></videoShowcase>
		<!-- <screenShot></screenShot> -->
		<sliderCar/>
		<!-- <mapShow></mapShow> -->
		<download></download>
		<footerContent></footerContent>
		<a class="btn btn-dark btn-fixed btn-lg" href="#download" style="background-color:black;">Download Now</a>
	</div>
</template>
<script>
	import navComponent from './Global/navComponent'
	import headerSlider from './Main/headerSlider'
	import aboutus from './Main/aboutus';
	import future from './Main/future';
	import showcase from './Main/showcase'
	import videoShowcase from './Main/videoShowcase'
	import screenShot from './Main/screenShot';
	import sliderCar from './Main/sliderCar';
	import mapShow from './Main/mapShow'
	import download from './Main/download'
	import footerContent from './Global/footerContent'
	export default {
		components:{navComponent,headerSlider,aboutus,future,showcase, videoShowcase,screenShot,download,footerContent,mapShow,sliderCar}
	}	
</script>
<style>
@font-face {
  font-family: "Nonchalance";
  src: url("../assets/fonts/noh/fontttf.eot"); /* IE9 Compat Modes */
  src: url("../assets/fonts/noh/fontttf.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../assets/fonts/noh/fontttf.otf") format("opentype"), /* Open Type Font */
    url("../assets/fonts/noh/fontttf.svg") format("svg"), /* Legacy iOS */
    url("../assets/fonts/noh/fontttf.ttf") format("truetype"), /* Safari, Android, iOS */
    url("../assets/fonts/noh/fontttf.woff") format("woff"), /* Modern Browsers */
    url("../assets/fonts/noh/fontttf.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}
body {
	 font-family: 'Cairo', sans-serif !important;
}
.btn-fixed{position: fixed; right: 20px; bottom: 20px;}
</style>


