<template>
    <div class="city">
        <navComponent />
        <allCountrysHeaderVue />


        <div class="container-fluid p-90">
            <div class="row">
                <div class="col-md-2" v-for="(i,index) in countrys" :key="index">
                    <ul class="list-group" >
                        <li class="list-group-item city-list-item"><a :href="'#/allCitys/' + i.title" class="cityLink">{{i.title}}</a></li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="mt-3"></div>
        <footerContent/>
    </div>    
</template>
<script>
import navComponent from '@/Components/Global/navComponent.vue';
import allCountrysHeaderVue from './components/allCountrysHeaderVue.vue';
import footerContent from '@/Components/Global/footerContent.vue';
export default {
    components:{navComponent,allCountrysHeaderVue, footerContent},
    data() {
        return {
            countrys:[]
        }
    },
    mounted(){
    const vm = this;
    switch(vm.$route.params.qara) {
        case 'Asia':
            vm.countrys = [
                {title:'Bahrain'},
                {title:'Bangladesh'},
                {title:'India'},
                {title:'Indonesia'},
                {title:'Iran'},
                {title:'Iraq'},
                {title:'Jordan'},
                {title:'KingdomOfBhutan'},
                {title:'Kuwait'},
                {title:'Lebanon'},
                {title:'Malaysia'},
                {title:'Oman'},
                {title:'Pakistan'},
                {title:'Qatar'},
                {title:'SaudiArabia'},
                {title:'SriLanka'},
                {title:'UnitedArabEmirates'},
                {title:'Yemen'},
            ];
            break;

        case 'Africa': 
            vm.countrys = [
                {title:'Algeria'},
                {title:'Egypt'},
                {title:'Libya'},
                {title:'Morocco'},
                {title:'Sudan'},
                {title:'Tunisia'},
            ];
            break;


        case 'Europe': 
            vm.countrys = [
                {title:'France'},
                {title:'Germany'},
                {title:'Italy'},
                {title:'Turkey'},
                {title:'UnitedKingdom'},
            ];
            break;


        case 'NorthAmerica': 
            vm.countrys = [
                {title:'Canada'},
                {title:'UnitedStates'},
                {title:'Mexico'},
            ];
            break;


        case 'SouthAmerica': 
            vm.countrys = [
                {title:'Chili'},
                {title:'Brasilia'},
            ];
            break;
    }

    }
}
</script>
<style>
.sliderCar { padding:40px 0px;}
.cityContent{font-weight: 600; font-size: 18px; text-align: center; padding:60px 0px;}
.boxess{padding: 0px 0px;}
.safteyBox{padding: 30px; background-color: rgba(0,0,0,0.1); border-radius: 5px; border:1px solid black;}
.sbHeader{text-align: left; font-weight: 600; font-size: 20px;}
.sbContent{font-weight: 700; text-align: left; padding-top: 10%; font-size: 20px;}
.sbHeaderR{text-align: right; font-weight: 600; font-size: 20px; }
.sbContentR{font-weight: 700; text-align: right;padding-top: 10%;font-size: 20px;}
.halfContent{padding:2% 0px; margin-top: 50px; background-attachment: fixed;background-position: center;background-repeat: no-repeat;background-size: cover;} 
.hCon{padding:5px; border-radius: 10px; background-color: rgba(0, 0, 0, 0.4); color:white;}
.videoShowcase{background-attachment: fixed;background-position: center;background-repeat: no-repeat;background-size: cover;  height: 1000px; background-color: black;}
.rydeCity{text-align: center; font-size: 28px; padding: 3px; background-color: rgba(255, 255, 255, 0.4); color: white; font-weight: 400;}
.city-list-item{background-color: rgba(255, 255, 255, 0.4); color: white; font-weight: 400; text-align: center; font-size: 18px; border-radius: 10px; margin-top:5px; cursor: pointer; transition: all 0.3s;}
.city-list-item:hover{background-color: rgba(255, 255, 255, 0.6);}
.cityLink{ text-decoration: none; color:white;}
.cityLink:hover{ text-decoration: none; color:white;}
.videoBackGroundContent{position:absolute;width:100%;height:100%;padding-top:75px;}
#myVideo{position: absolute;width:100%;height:100%;}
.p-90{padding:90px 0px; background-color: black;}
</style>


