<template>
    <div class="aboutusPages">
        <navComponent/>
        <header-vue/>
        <div class="topLabelAbout" >
            <div data-aos="fade-up">
                Rydes is a provider of transport services through its website and its smart mobile applications.
            </div>
        </div>
        <slicerOne/>
        <team/>
        <our/>
        <!-- <qute/> -->
        <div class="topLabelAbout" style="color:#4bb6f8; font-family:'Nonchalance';" >
            <div data-aos="fade-up">
            Ryde
            </div>
        </div>
        <service/>
        <contactus/>
        <footerContent/>
    </div>    
</template>
<script>
import navComponent from '@/components/Global/navComponent.vue';
import headerVue from './aboutus/headerVue.vue';
import slicerOne from './aboutus/slicerOne.vue';
import team from './aboutus/team.vue';
import our from './aboutus/our.vue';
import qute from './aboutus/qute.vue';
import service from './aboutus/service.vue';
import contactus from './aboutus/contactus.vue'
import footerContent from '@/components/Global/footerContent.vue';

export default {
    components:{navComponent,headerVue,slicerOne,team,our,qute,service,contactus,footerContent}
    }
</script>
<style>
@font-face {
  font-family: "Nonchalance";
  src: url("../assets/fonts/noh/fontttf.eot"); /* IE9 Compat Modes */
  src: url("../assets/fonts/noh/fontttf.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../assets/fonts/noh/fontttf.otf") format("opentype"), /* Open Type Font */
    url("../assets/fonts/noh/fontttf.svg") format("svg"), /* Legacy iOS */
    url("../assets/fonts/noh/fontttf.ttf") format("truetype"), /* Safari, Android, iOS */
    url("../assets/fonts/noh/fontttf.woff") format("woff"), /* Modern Browsers */
    url("../assets/fonts/noh/fontttf.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}
.topLabelAbout{padding:20px 0px; text-align: center; font-weight: 700; font-size: 26px; background-color: rgba(0,0,0,0.9); color:white;}
</style>


